
'use client';

import { useEffect } from 'react';
import { useRouter } from 'next/navigation';
import { useUser, useDoc, useMemoFirebase, useFirestore } from '@/firebase';
import { doc } from 'firebase/firestore';
import { usePathname } from 'next/navigation';

/**
 * Custom hook to redirect users based on their authentication and onboarding status.
 */
export function useAuthRedirect() {
  const { user, isUserLoading } = useUser();
  const router = useRouter();
  const pathname = usePathname();
  const firestore = useFirestore();

  const userDocRef = useMemoFirebase(() => (user ? doc(firestore, 'users', user.uid) : null), [user, firestore]);
  const { data: userProfile, isLoading: isProfileLoading } = useDoc<{
    onboardingCompleted: boolean;
  }>(userDocRef);

  useEffect(() => {
    const isLoading = isUserLoading || isProfileLoading;
    if (isLoading) {
      return; // Wait until all data is loaded
    }

    if (user && userProfile) {
      const onAuthPage = pathname === '/login' || pathname === '/signup';
      const onOnboardingPage = pathname === '/onboarding';

      if (userProfile.onboardingCompleted) {
        if (onAuthPage || onOnboardingPage) {
          router.push('/'); // Fully onboarded users shouldn't be on auth or onboarding pages
        }
      } else {
        if (!onOnboardingPage) {
          router.push('/onboarding'); // If not onboarded, force them to the onboarding page
        }
      }
    } else if (!user && !isLoading) {
        // Not logged in, but trying to access a protected page
        const protectedPages = ['/profile', '/onboarding'];
        if (protectedPages.includes(pathname)) {
            router.push('/login');
        }
    }
  }, [user, userProfile, isUserLoading, isProfileLoading, router, pathname]);
}
