
'use server';

import { doc, getDoc, setDoc, serverTimestamp } from 'firebase/firestore';
import { firestore } from './firebase';

/**
 * Creates a deterministic, unique chat ID for any two users.
 * @param uid1 - The first user's ID.
 * @param uid2 - The second user's ID.
 * @returns A unique string ID for the chat.
 */
function getChatId(uid1: string, uid2: string): string {
  // Sort the UIDs to ensure the ID is the same regardless of who initiates the chat.
  return uid1 < uid2 ? `${uid1}_${uid2}` : `${uid2}_${uid1}`;
}

/**
 * Initiates a chat between two users if one doesn't already exist.
 * @param currentUserId - The ID of the user initiating the chat.
 * @param otherUserId - The ID of the user to chat with.
 * @returns The ID of the chat room.
 */
export async function initiateChat(currentUserId: string, otherUserId: string): Promise<string> {
  const chatId = getChatId(currentUserId, otherUserId);
  const chatDocRef = doc(firestore, 'chats', chatId);

  try {
    const chatDoc = await getDoc(chatDocRef);

    if (!chatDoc.exists()) {
      // If the chat doesn't exist, create it.
      await setDoc(chatDocRef, {
        id: chatId,
        members: [currentUserId, otherUserId],
        createdAt: serverTimestamp(),
        lastMessage: null,
        lastMessageTimestamp: null,
      });
    }
    
    return chatId;
  } catch (error) {
    console.error("Error initiating chat:", error);
    // Re-throw the error to be handled by the calling function.
    throw new Error("Failed to create or retrieve chat.");
  }
}
